/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	display: none;
}

// IE
input[type="search"]::-ms-clear {
	display: none;
}

input[type=text]::-ms-clear {
	display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.mkdf-sidebar {
	position: relative;
	z-index: 11;
	margin: 0 0 60px;

	.widget {

		&:last-child {
			margin: 0 !important;
		}
	}
}

.wpb_widgetised_column {
	position: relative;

	.widget {

		&:last-child {
			margin: 0 !important;
		}
	}
}

/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget.mkdf-recent-post-widget {
	.mkdf-widget-title {
		margin: 0 0 32px;
	}

	ul {

		.mkdf-rp-item {
			margin-bottom: 20px;

			a:hover {
				.mkdf-rp-title {
					color: $first-main-color;

				}
			}

			.mkdf-rp-image {
				display: inline-block;
				float: left;
				width: 60px;
				height: 60px;
				margin-right: 20px;

				img {
					border-radius: 7px;
				}
			}

			.mkdf-rp-text {
				display: inline-block;
				float: right;
				height: 60px;
				width: calc(100% - 80px);
				overflow: hidden;
			}

			.mkdf-rp-title {
				font-size: 16px;
				line-height: 22px;
				height: 20px;
				font-family: $default-heading-font;
				font-weight: 400;
				text-transform: capitalize;
				letter-spacing: .125em;
				margin: 7px 0 0;
				overflow: hidden;
				@include mkdfTransition(color 0.2s cubic-bezier(0.02, 0.54, 0.58, 1));
			}

			.mkdf-rp-date {
				position: absolute;
				color: #938da6;
				font-size: 11px;
				font-family: $default-heading-font;
				font-weight: 400;
				text-transform: uppercase;
				letter-spacing: .125em;
				height: 22px;
				overflow: hidden;
			}
		}

	}
}

footer {
	.widget {
		&.widget_pages,
		&.widget_archive,
		&.widget_categories,
		&.widget_meta,
		&.widget_recent_comments,
		&.widget_nav_menu {

			ul {

				li {
					color: #fff;
				}
			}
		}
	}
}

.mkdf-content .widget.widget_mkdf_twitter_widget .mkdf-twitter-widget.mkdf-twitter-speech-bubble li .mkdf-tweet-text {
	background: rgba(195, 209, 226, .2);
}

.mkdf-content .widget.widget_mkdf_twitter_widget .mkdf-twitter-widget.mkdf-twitter-speech-bubble li .mkdf-tweet-text:after {
	border-color: rgba(195, 209, 226, .2) transparent transparent transparent;
}

.widget {
	margin: 0 0 50px;

	&.widget_text {
		margin: 0 0 32px;

		.mkdf-widget-title {
			margin: 9px 0 20px;
			letter-spacing: 0;
		}

	}

	.mkdf-widget-title {
		margin: 0 0 17px;

		a {
			color: inherit;
		}
	}

	ul {
		padding: 0;
		margin: 0;
		list-style: none;

		li {
			margin: 0 0 10px;

			&:last-child {
				margin: 0;
			}
		}
	}

	/*
	*   Specific styles
	*/
	&.widget_text {

		img {
			display: block;
		}
	}

	#wp-calendar {
		width: 100%;
		text-align: center;

		caption {
			color: $default-heading-color;
			margin: 0 0 15px;
		}

		th, td {
			padding: 3px 4px;
		}

		td#today {
			color: #fff;
			background-color: $first_main_color;

			a {
				color: inherit;
			}
		}
	}

	&.widget_rss {

		.mkdf-widget-title {

			.rsswidget {

				&:hover {
					color: $first-main-color;
				}
			}
		}

		.rss-widget-icon {
			display: inline-block;
		}

		ul {

			li {
				margin: 0 0 25px;

				.rss-date {
					display: block;
				}
			}
		}
	}

	&.widget_search {

		form {
			@include mkdfRelativeHolderLayout();
			clear: both;

			> div {
				@include mkdfRelativeHolderLayout();
				background-color: #7bb9ff;
				border-radius: 25px;
				border: 0;
				box-sizing: border-box;
			}
		}

		input,
		button {
			position: relative;
			display: inline-block;
			vertical-align: top;
			float: left;
			height: 50px;
			line-height: 50px;
			color: #fff;
			background-color: transparent;
			border: 0;
			border-radius: 0;
			outline: 0;
			-webkit-appearance: none;
			box-sizing: border-box;
			@include mkdfTransition(color .2s cubic-bezier(0.02, 0.54, 0.58, 1));
		}

		input {
			width: calc(100% - 75px);
			padding: 10px 0 10px 30px;
			font-size: 11px;
			font-family: $default-heading-font;
			font-weight: 500;
			text-transform: uppercase;
			letter-spacing: .125em;
			box-sizing: border-box;

			@include mkdfPlaceholder {
				color: inherit;
			}
		}

		button {
			width: 75px;
			font-size: 13px;
			cursor: pointer;
			@include mkdfTransition(color .2s cubic-bezier(0.02, 0.54, 0.58, 1));

			&:hover {
				color: $default-heading-color;
			}

			span {
				display: block;
				line-height: inherit;

				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
	}

	&.widget_pages,
	&.widget_archive,
	&.widget_categories,
	&.widget_meta,
	&.widget_recent_comments,
	&.widget_nav_menu {

		ul {

			li {
				margin: 2px 0;
				color: $default-heading-color;
				font-size: 11px;
				font-family: $default-heading-font;
				font-weight: 400;
				text-transform: uppercase;
				letter-spacing: .125em;

				&:last-child {
					border: none;
				}

				ul {
					margin: 4px 0 0;
					padding: 0 0 0 10px;

					li {

						&:last-child {
							padding: 4px 0 0;
						}
					}
				}
			}
		}
	}

	&.widget_recent_entries {

		.mkdf-widget-title {
			margin: 0 0 23px;
		}

		ul {

			li {
				margin: 0 0 12px;

				> a {
					font-size: 16px;
					font-family: $default-heading-font;
					font-weight: 400;
					text-transform: capitalize;
					letter-spacing: .125em;
				}

				> span {
					display: block;
					color: #938da6;
					font-size: 11px;
					font-family: $default-heading-font;
					font-weight: 400;
					text-transform: uppercase;
					letter-spacing: .125em;
					height: 22px;
				}
			}
		}
	}

	&.widget_archive,
	&.widget_categories,
	&.widget_text {

		select {
			width: 100%;
			max-width: 100%;
			height: 54px;
			padding: 0 18px;
			line-height: 54px;
			color: $default-text-color;
			background-color: transparent;
			border: 1px solid $default-border-color;
			font-family: inherit;
			font-size: inherit;
			font-style: inherit;
			font-weight: inherit;
			outline: none;
			box-sizing: border-box;
		}
	}

	&.widget_categories {
		.mkdf-widget-title {
			margin: 0 0 22px;
		}
	}

	&.posts_holder {

		ul {

			li {

				&.page_item_has_children {

					& > a {
						display: block;
					}

					& > ul.children {
						padding-left: 10px;
					}
				}

				&.menu-item-has-children {

					& > a {
						display: block;
					}

					& > ul.sub-menu {
						padding-left: 10px;
					}
				}
			}
		}
	}

	&.widget_tag_cloud {

		.tagcloud {
			margin-top: 30px;
		}

		a {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			font-family: Poppins, sans-serif;
			font-size: 11px !important;
			line-height: 2em;
			letter-spacing: .125em;
			border-radius: 3em;
			font-weight: 500;
			text-transform: uppercase;
			outline: 0;
			box-sizing: border-box;
			padding: 8px 28px;
			color: #938da6;
			background-color: #f5f9ff;
			border: 0;
			white-space: nowrap;
			cursor: pointer;
			margin: 0 6px 10px 0;
			@include mkdfTransition(color .2s cubic-bezier(0.02, 0.54, 0.58, 1), background-color .2s cubic-bezier(0.02, 0.54, 0.58, 1));
		}

		a:hover {
			color: #fff;
			background-color: $first-main-color;
		}

	}

	&.widget_media_image {
		margin-bottom: 20px;

		.mkdf-widget-title {
			margin: 0 0 32px;
		}

		a, img {
			display: block;
		}
	}
}

.vc_wp_search {

	.widget {

		&.widget_search {

			form {

				> div {
					background-color: #fff;
					border: none;
				}
			}

			input {
				width: calc(100% - 66px);
				padding: 0 0 0 33px;
				font-size: 15px;
