@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
	@include mkdfRelativeHolderLayout();
	padding: 0;
	box-sizing: border-box;

	.mkdf-comment-holder-inner {
		padding: 30px 0 20px;
	}

	.mkdf-comment {
		background-color: #f5f9ff;
		border-radius: 8px;
		margin-bottom: 30px;

		&.mkdf-pingback-comment {

			.mkdf-comment-text {
				padding: 0;
			}
		}
	}

	.mkdf-comment-list {
		list-style: none;
		position: relative;
		z-index: 150;
		padding: 0;
		margin: 0;

		li {
			@include mkdfRelativeHolderLayout();
		}

		> li:first-child {
			margin: 0;
			padding: 0;
			border: 0;
		}

		.children {
			padding: 0 0 0 81px;
		}
	}

	.mkdf-comments-title {
		margin-bottom: 28px;

		> * {
			margin: 0;
		}
	}

	.mkdf-comment-image {
		float: left;
		padding: 50px;

		img {
			border-radius: 50%;
			height: 120px;
			width: 120px;
		}
	}

	.mkdf-comment-text {
		float:left;
		padding: 50px 50px 50px 0;
		width: calc(100% - 270px);

		.mkdf-comment-info {
			@include mkdfRelativeHolderLayout();
		}

		.replay,
		.comment-reply-link,
		.comment-edit-link {
			float: left;
			font-family: $default-heading-font;
			font-size: 11px;
			text-transform: uppercase;
			letter-spacing: .125em;
			font-weight: 400;
			margin-right: 50px;

			&:hover {
				color: $first-main-color;
			}

			&:after {
				font-family: 'ElegantIcons';
				content: "\24";
				font-size: 1.625em;
				vertical-align: middle;
				margin-left: 10px;
			}
		}

		.mkdf-comment-name {
			display: inline-block;
			margin: 0 25px 0 0;
		}

		.mkdf-comment-date {
			display: inline-block;
			@include mkdfTextColorLightPurple;

		}

		.comment-respond {
			margin: 20px 0;

			.comment-reply-title {
				margin: 0 0 10px;
				font-size: 14px;
			}

			.logged-in-as {
				margin: 0 0 8px;
			}
		}

		#cancel-comment-reply-link {
			margin-left: 10px;
			font-size: 12px;
			color: $first-main-color;

			&:hover {
				color: $default-heading-color;
			}
		}

		ul, ol {

			li {
				display: list-item;
			}
		}

		li {
			margin: 0 !important;
			padding: 0 !important;
			border: 0 !important;
		}
	}
}

.mkdf-comment-form {
	padding: 0;
	clear: both;
	box-sizing: border-box;

	.comment-form-cookies-consent {
		margin: 0px 0 23px;
	}

	.mkdf-comment-form-inner {
		padding: 45px 0 78px;
	}

	.comment-reply-title {
		margin: 0 0 37px;
	}

	.logged-in-as {
		margin: 0 0 8px;
	}
}

@include ipad-portrait {

	.mkdf-comment-holder {

		.mkdf-comment-list {

			.children {
				padding: 0 0 0 40px;
			}
		}
	}
}

@include phone-landscape {

	.mkdf-comment-holder {

		.mkdf-comment-list {

			.children {
				padding: 0;
			}
		}

		.mkdf-comment-text {

			.replay,
			.comment-reply-link,
			.comment-edit-link {
				float: none;
				margin: 0 10px 0 0;
			}
		}
	}
}

@include phone-portrait {

	.mkdf-comment-holder {

		.mkdf-comment-image {
			float: none;
		}

		.mkdf-comment-text {
			padding: 20px 0 0;
			min-height: inherit;
		}
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;

		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include mkdfButtonDefaultStyle();
	@include mkdfButtonSize();
	@include mkdfButtonSolidColor();
	cursor: pointer;

	&:hover {
		@include mkdfButtonSolidHoverColor();
	}
}
input.wpcf7-form-control.wpcf7-submit.notify-me-button {
	padding: 12px 28px;
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
}

.mkdf-two-columns-form-without-space {

	.wpcf7-form .ajax-loader {
		display: none;
	}
}

.mkdf-cf7-btn-custom-hover {
	input.wpcf7-form-control.wpcf7-submit {
        @include mkdfTransition(box-shadow .35s cubic-bezier(0.02, 0.54, 0.58, 1));
		background-color:  $first-main-color !important;

		&:hover {
			box-shadow: 0px 10px 25px 0px rgba($first-main-color, 0.5);
		}
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
	visibility: hidden;

	.owl-stage-outer {
		z-index: 1;
	}

	&.mkdf-slider-is-moving {

		.owl-item {

			.mkdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}

	&.mkdf-slider-has-both-nav {

		.owl-nav {

			.owl-prev,
			.owl-next {
				$pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
				@include mkdfTransform(translateY($pos));
			}
		}
	}

	&.mkdf-slider-hide-pagination .owl-dots  {
		display: none;
	}

	.owl-nav {

		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			padding: 0;
			margin: 0;
			color: $default-heading-color;
			background: none;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;
			z-index: 3;
			@include mkdfTransform(translateY(-50%));
			@include mkdfTransition(color .2s cubic-bezier(0.02, 0.54, 0.58, 1));

			&:hover {
				color: $first-main-color;
			}

			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 46px;
				line-height: 1;

				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}

		.owl-prev {
			left: 30px;
		}

		.owl-next {
			right: 30px;
		}
	}

	.owl-dots {
		text-align: center;
		margin: 18px 0 0;

		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			padding: 0;
			margin: 0 10px;
			background: none;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;

			span {
				display: inline-block;
				vertical-align: middle;
				width: 10px;
				height: 10px;
				border-radius: 3em;
				background-color: transparent;
				box-sizing: border-box;
				@include mkdfTransition(background-color .2s cubic-bezier(0.02, 0.54, 0.58, 1), border .2s cubic-bezier(0.02, 0.54, 0.58, 1));
			}

			&:nth-child(3n) {
				span {
					background-color: $second-main-color;
				}
			}

			&:nth-child(3n + 1) {
				span {
					background-color: $first-main-color;
				}
			}

			&:nth-child(3n + 2) {
				span {
					background-color: $default-heading-color;
				}
			}

			&:hover,
			&.active {

				span {
					opacity: 0.8;
				}
			}
		}
	}

	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


	+ .mkdf-slider-thumbnail {
		display: inline-block;
		vertical-align: middle;
		list-style: none;
		margin: 20px -10px 0;

		> .mkdf-slider-thumbnail-item {
			float: left;
			padding: 0 10px;
			margin-bottom: 20px;
			box-sizing: border-box;
			line-height: 0;
			cursor: pointer;

			img {
				border: 2px solid transparent;
				@include mkdfTransition(border-color 0.2s cubic-bezier(0.02, 0.54, 0.58, 1));
			}

			&.active {
				img {
					border: 2px solid $first-main-color;
				}
			}
		}

		$thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

		@for $i from 0 to length($thumbnail_columns_number) {
			&.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

				.mkdf-slider-thumbnail-item {
					width: 100% / ($i+2);

					@include ipad-landscape {
						width: 33.3333%;
					}
				}
			}
		}


	}

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

	.mkdf-wrapper {
		padding: 0 !important;
	}

	.mkdf-content {
		height: 100vh;
	}

	&.admin-bar {

		.mkdf-content {
			height: calc(100vh - 32px);
		}
	}

	.mkdf-content-inner {
		@include mkdfTableLayout();
	}

	.mkdf-page-not-found {
		@include mkdfTableCellLayout();
		padding: 0 16%;
		text-align: left;

		.mkdf-404-title {
			margin: 0;
			margin-left: -4px;

			@include ipad-landscape{
				font-size: 2em;
				text-shadow:
						-1px -1px 0 #fff,
						1px -1px 0 #fff,
						-1px 1px 0 #fff,
						1px 1px 0 #fff;
			}
		}

		.mkdf-404-subtitle {
			margin: 35px 0 0;
			display: inline-block;

			@include ipad-landscape{
				font-size: 2em;
				text-shadow:
						-1px -1px 0 #fff,
						1px -1px 0 #fff,
						-1px 1px 0 #fff,
						1px 1px 0 #fff;
			}
		}

		.mkdf-404-text {
			margin: 16px 0 0;
			color: #716c80;
			font-size: 20px;
			max-width: 440px;

			@include ipad-landscape{
				text-shadow:
						-1px -1px 0 #fff,
						1px -1px 0 #fff,
						-1px 1px 0 #fff,
						1px 1px 0 #fff;
			}
		}

		.mkdf-btn {
			margin: 50px 0 0;
			line-height: 3em;

			&.mkdf-btn-icon>span:not(.mkdf-btn-text) {
				left: -3px;
				margin: 0;
				width: 7px;
			}

			&.mkdf-btn-light-style {
				color: #585858;
				background-color: #fff;
				border-color: #fff;

				&:hover {
					color: #fff !important;
					background-color: transparent !important;
					border-color: #fff !important;
				}
			}
		}
	}

	@include laptop-landscape-mac {
		.mkdf-page-not-found {
			padding: 80px 10% 0
		}
	}

	@include ipad-landscape {
		.mkdf-page-not-found {
			padding: 35% 12% 20px;
		}

		.mkdf-content {
			height: 100%;
			min-height: 100vh;
		}

		&.admin-bar {

			.mkdf-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}
	}

	@include ipad-portrait {

		.mkdf-page-not-found {
			padding: 30% 85px 20px;
		}
	}

	@include phone-portrait {
		.mkdf-page-not-found {
			padding: 299px 30px 20px;
		}
	}
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
	$size: 40px;

	position: fixed;
	width: $size;
	height: $size;
	right: $size;
	bottom: $size;
	z-index: 10000;
	opacity: 0;
	visibility: hidden;
	
	&.off {
		opacity: 0;
		visibility: hidden;
		@include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
	}
	
	&.on {
		opacity: 1;
		visibility: visible;
		@include mkdfTransition(opacity 0.3s ease);
	}

	.mkdf-btt-wrapper {
		@include mkdfAbsoluteHolderLayout();
		background-color: $first-main-color;
		border-radius: 4px;
		@include mkdfTransition(.25s);
	}
	
	svg {
		fill: #fff;
		@include mkdfTransformOrigin(50% 50%);
	}

	&:hover {
		.mkdf-btt-wrapper {
			box-shadow: 0 5px 12px 0 rgba(0,0,0,0.23);
			@include mkdfTransform(translateY(-10px));
		}
	}
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.mkdf-self-hosted-video-holder {

	.mkdf-video-wrap {

		.mejs-poster,
		.mejs-container,
		.mejs-overlay-play {
			height: 100% !important; /*!important because of the inline style*/
			width: 100% !important; /*!important because of the inline style*/
		}
	}
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */
   
/* ==========================================================================
   #Dynamic Backgrounds styles - begin
   ========================================================================== */

#mkdf-dynamic-bgrnds {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
	width: 100vw;
	@include mkdfTransition(background-color 1s);
}

/* ==========================================================================
   #Dynamic Backgrounds styles - end
   ========================================================================== */